@import "partials/mixins";

@font-face {
	font-family: "ProximaNova-Regular";
	src: url("../fonts/ProximaNova/ProximaNova-Regular.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Regular";
	src: url("../fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Bold";
	src: url("../fonts/ProximaNova/ProximaNova-Bold.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Bold";
	src: url("../fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Light";
	src: url("../fonts/ProximaNova/ProximaNova-Light.ttf") format("truetype");
}

$desk-regular-proxima: "ProximaNova-Regular", sans-serif;
$desk-bold-proxima: "ProximaNova-Bold", sans-serif;
$desk-regular-raleway: "Raleway-Regular", sans-serif;
$desk-bold-raleway: "Raleway-Bold", sans-serif;
$desk-light-proxima: "ProximaNova-Light", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
$arrowcolor: #E78B48;

body {
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: #FBFBFB;
	color: #333;
	font-family: 'ProximaNova-Regular','ProximaNova-Bold','Raleway-Regular','Raleway-Bold', sans-serif;
	font-size: 14px;
	line-height: auto;
}

.home_block {
	width: 100%;
	height: 700px;
	position: relative;
}

.logo_img {
	position: absolute;
	top: 10%;
	left: 40%;
	display: inline-block;
}

.links {
	position: absolute;
	width: 300px;
	height: 600px;
	top: 20%;
	left: 40%;
	list-style-type: none;
	text-align: left;

	li {
		margin-left: 0%;
		width: 300px;
		color: #7c7c7c;
		font-size: 16px;
		line-height: 28px;
		font-family: $desk-regular-raleway;
		margin-bottom: 10px;
	}
}

.links li a {
	text-decoration: none;
	color: inherit;
	font-family: inherit;

	&:hover,
	&:focus {
		color: $arrowcolor;
	}
}

